<template>
    <el-button :type="type" :icon="icon" :disabled="disabled" :size="size" @click="exportData">{{title}}</el-button>
</template>
<script>
  export default {
    props: {
      title: {
        type: String,
        default: ''
      },
      action: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      type: {
        type: String,
        default: 'primary'
      },
      icon: {
        type: String,
        default: 'el-icon-download'
      },
      size: {
        type: String,
        default: 'mini'
      }
    },
    mounted() {
    },
    methods:{
      //导出
      exportData() {
        const that = this;
        that.showConfirm('确定要'+this.title+'数据吗?', function () {
            location.href = that.action;
            that.showSuccess("导出成功！")
        });
      },
    }
  }
</script>



